export const lambdaHandler = async (
  _event: any,
  _context: any,
): Promise<{ statusCode: number; body: string }> => {
  try {
    return {
      statusCode: 200,
      body: JSON.stringify({
        he: JSON.parse(_event.body).greeting,
      }),
    };
  } catch (err) {
    console.log(err);
    throw err;
  }
};
